:root{
  --bg-color:#081b29;
  --second-bg-color:#112e42;
  --text-color:#ededed;
  --main-color:#00abf0;
}

html{
  font-size: 62.5%;
  color:var(--text-color)
}

body{
  background: var(--bg-color);
  color: var(--text-color);
}

.heading{
  font-size: 5rem;
  margin-bottom: 3rem;
  text-align: center;
}

span{
  color: var(--main-color);
}

.btn-box.btns{
  display: inline-block;
  width: 15rem;
}

.btn-box.btns a::before{
  background: var(--second-bg-color);
}


.btn-box{
  position: relative;
  display: flex;
  justify-content: space-between;
  width: 34.5rem;
  height: 5rem;
}

.btn-box .btn{
  position: relative;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 15rem;
  height: 100%;
  background: var(--main-color);
  border: .2rem solid var(--main-color);
  border-radius: .8rem;
  font-size: 1.8rem;
  font-weight: 600;
  letter-spacing: .1rem;
  color: var(--bg-color);
  z-index: 1;
  overflow: hidden;
  transition: .5s;
}





@media (max-width:1200px){
  html{
    font-size: 55%;
  }
}

@media (max-width: 991px) {
  .header{
    padding: 2rem 4%;
  }
  section{
    padding:10rem 4% 2rem;
  }
  .home{
    padding: 0 4%;
  }
  .footer{
    padding: 2rem 4%;
  }
}

