section{
    min-height: 100vh;
    padding: 10rem 9% 2rem;
}
.home{
    display: flex;
    align-items: center;
    padding: 0 9%;
    /* background: url('../images/home.jpg') no-repeat;
    background-size: cover;
    background-position: center; */
}
.home-content{
    max-width: 60rem;
    float: left;
}

/* .home-content img{
    float: left;
} */
.image-set img{
    float:left;
    /* margin-top: 100px; */
    /* margin-right: 200px; */
    height: 420px;
    border: 5.2rem solid var(--bg-color);
    border-radius: 30%;
}


.image-set img:hover{
    opacity: .8;
    height: 430px;   
}

.home-content h1{
    font-size: 5.6rem;
    font-weight: 700;
    line-height: 1.3;
}

.home-content h1 span{
    color: var(--text-color);
}

.home-content .text-animate{
    position: relative;
    width:32.8rem;
}
.home-content .text-animate h3{
    font-size: 3.1rem;
    font-weight:700;
    color:transparent;
    -webkit-text-stroke: .7px var(--main-color);
    background-image: linear-gradient(var(--main-color),var(--main-color));
    background-repeat: no-repeat;
    -webkit-background-clip: text;
    background-position: 0 0;
    animation: homeBgText 6s linear infinite;
    animation-delay: 2s;
}

.home-content .text-animate h3::before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 0;
    height: 100%;
    /* border-right: 2px solid var(--main-color); */
    z-index: -1;
    animation: homeCursorText 6s linear infinite;
    animation-delay: 2s;
}

.home-content p{
    font-size: 1.6rem;
    margin: 2rem 0 4rem;
    text-align: justify;
}



.btn-box .btn:hover{
    color: var(--main-color);
}

.btn-box .btn:nth-child(2){
    background: transparent;
    color: var(--main-color);
}

.btn-box .btn:nth-child(2):hover{
    color:var(--bg-color);
}

.btn-box .btn:nth-child(2)::before{
    background: var(--main-color);
}

.btn-box .btn::before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 0 ;
    height: 100%;
    background:var(--bg-color);
    z-index: -1;
    transition: .5s;
}

.btn-box .btn:hover::before{
    width: 100%;
}

.home-sci{
    position:absolute;
    bottom: 4rem;
    width: 170px;
    display: flex;
    justify-content: space-between;
    margin-left: 425px;
}

.home-sci a{
    position: relative;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    background: transparent;
    border: .2rem solid var(--main-color);
    border-radius: 50%;
    font-size: 20px;
    color: var(--main-color);
    z-index: 1;
    overflow: hidden;
    transition: .5s;
}

.home-sci a:hover{
    color: var(--bg-color);
}

.home-sci a::before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 0;
    height: 100%;
    background:var(--main-color);
    z-index: -1;
    transition: .5s;
}

.home-sci a:hover::before{
    width: 100%;
}

/* KEYFRAMES ANIMATION */

@keyframes homeBgText {
 0%,10%,100%{
    background-position: -33rem 0;
 } 
 65%,
 85%{
    background-position: 0 0;
 }  
}

@keyframes homeCursorText {
    0%,10%,100%{
       width: 0;
    } 
    65%,
    78%,
    85%{
       width: 100%;
       opacity: 1;
    } 
    
    75%,81%{
        opacity: 0;
    }
   }